"use client";
import "./projects.css";
import PDFViewer from "../components/pdfViewer";
import { useState } from "react";
import { motion as m } from "motion/react"
import { AnimatePresence } from "motion/react";
import ProjectButton from "../components/projectButton"


const buttonVariants = {
    initial: {
        background: "var(--foreground)",
        color: "var(--copy)",
        scale: 1
    },
    hovered: {
        background: "var(--primary)",
        color: "var(--primary-content)",
        scale: 1.02,
    },
    tapped: {
        background: "var(--primary)",
        color: "var(--primary-content)",
        scale: 0.98,
    },
    selected: {
        background: "var(--primary)",
        color: "var(--primary-content)",
        scale: 1.02,
    },
}


const classContainerVariants = {
    initial: {
        height: "50px",
        border: "0px solid var(--border)"

    },
    open: {
        height: "195px",
        background: "var(--foreground)",
        border: "1px solid var(--border)"

    }

}

const projects = [
    {
        id: "Aerospace",
        title: "Transducer Calibration",
        description: "This is my first project",
        pdf: "/project1.pdf",
    },
    {
        id: "Aerospace",
        title: "Circulation Around a 2-D Airfoil",
        description: "This is my second project",
        pdf: "/project2.pdf",
    },
    {
        id: "Aerospace",
        title: "Air Pressure Distribution",
        description: "This is my second project",
        pdf: "/project3.pdf",

    },
    {
        id: "Aerospace",
        title: "Lift and Drag of a Finite Wing",
        description: "This is my second project",
        pdf: "/project3.pdf",

    },
];

const classes = [
    "Aerospace Engineering",
    "Mechanical Engineering",
    "Software Engineering",
]

export default function Projects() {
    const [showProject, setShowProject] = useState(false); // Show Project Popup
    const [project, setProject] = useState(null);          // Index of Project 
    const [classCode, setClassCode] = useState("Aerospace Engineering")
    const [showSelectClass, setShowSelectClass] = useState("hide")

    const handleShowProject = (index) => {
        setProject(index)
        setShowProject(true)
    }

    const handleShowClasses = () => {

    }

    const handleClassChange = (index) => {
        setClassCode(classes[index]);
        setShowSelectClass(false);
    }

    return (
        <div className="page">
            <div className="projects">
                <div className="projects-header">
                    <h2 className="ac-header">Academic Projects</h2>
                    <m.div
                        className="class-select-container"
                        variants={classContainerVariants}
                        initial="initial"
                        animate={showSelectClass ? "open" : "initial"}
                        transition={{ duration: 0.5 }}
                    >

                        <m.button
                            className="selected-class"
                            variants={buttonVariants}
                            initial="initial"
                            whileHover="hovered"
                            whileTap="tapped"
                            onClick={() => setShowSelectClass(!showSelectClass)}
                        >
                            <p>{classCode}</p>
                        </m.button>
                        {classes.map((c, index) => (
                            <m.button
                                key={`c-button-key-${index}`}
                                variants={buttonVariants}
                                initial="initial"
                                whileHover="hovered"
                                whileTap="tapped"
                                animate={c === classCode ? "selected" : "initial"}
                                onClick={() => handleClassChange(index)}
                            >{c}</m.button>
                        ))}
                    </m.div>
                </div>
                <hr className="page-line" />
                <div className="project-grid">
                    {projects.map((project, index) => (
                        <div key={`p-div-key-${index}`} >
                            <ProjectButton projectName={project} />
                            
                        </div>
                        
                    ))}
                </div>
            </div>
        </div>
    );
}
