import '../componentstyles/projectbutton.css';
import { motion as m } from "motion/react"
import React, {useState} from "react";
import PDFViewer from './pdfViewer';

export default function ProjectButton(props) {
    const project = props.projectName;
    const showProject = props.projectNme;
    const setShowProject = props.setShowProject; 
    const index = props.index;

    const handleShowProject = () => {
        setShowProject(true); 
    };

    console.log(project); 

    return (
        <div>
            <m.button
                className="pbutton"
                onClick={handleShowProject} 
            >
                {project.title} {/* Render the project title */}
            </m.button>
            {showProject === index && (
                <PDFViewer
                    pdfUrl={`/project${index}.pdf`} 
                    showPDF={true} // Show the PDF viewer
                    setShowPDF={() => setShowProject(false)} 
                />
            )}
        </div>
    );
}
